    .para p {
        font-size: 14px;
    }
    .contentAbout .midHeading {
        margin-left: 30px;
    }
    .inputDOM input{
        width: 150px;
        height: 35px;
        border-radius: 10px;
    }
    .inputDOM input::-webkit-input-placeholder{
        font-size: 10px;
    }
    .inputDOM i , .inputDOM input::placeholder{
        font-size: 10px;
    }
    .inputDOM input::-webkit-input-placeholder{
        padding-left: 20px;
